/*
对话框样式
*/
.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: none;
  z-index: 101;
  overflow: hidden;
  overflow-scrolling: touch;
}

.modal .modal-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: #000;
  opacity: .3;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=30);
}

.modal .fade-in {
  animation: fadeIn .5s linear  both;
}

.modal .fade-in-mobile {
  animation: fadeIn .01s linear  both;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    top: -60px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}

.modal .fade-out {
  animation: fadeOut .5s linear  both;
}

.modal .fade-out-mobile {
  animation: fadeOut .5s linear  both;
}

@keyframes fadeOut {
  100% {
    opacity: 0;
    top: -60px;
  }
  0% {
    opacity: 1;
    top: 0;
  }
}

.modal-open {
  overflow: hidden;
  padding-right: 13px;
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

.modal-content {
  position: relative;
  width: 480px;
  margin: 80px auto;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
  pointer-events: fill;
}

.modal-sm .modal-content {
  width: 340px;
  border-radius: 5px;
  margin-top: 180px;
}

.modal-lg .modal-content {
  width: 900px;
  margin-top: 40px;
}

.modal-title {
  padding: 0 14px;
  text-align: center;
  font-size: 16px;
  height: 46px;
  line-height: 46px;
  background: #f4f4f4;
  border-radius: 3px 3px 0 0;
}

.modal-title .close {
  float: right;
  font-size: 25px;
  font-weight: 700;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  filter: alpha(opacity=50);
  opacity: .5;
  cursor: pointer;
  position: relative;
  top: -4px;
  right: -10px;
  font-family: "simsun";
  transition: .4s ease-in-out;
}

.modal-title .close:hover {
  transform: rotate(360deg);
}

.modal-title h4 {
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  color: #666;
}

.modal-body {
  padding: 14px 16px;
  font-size: 14px;
}

.modal-tip-box {
  text-align: center;
  margin: -30px 0 14px;
  font-size: 16px;
}

.modal-tip {
  display: inline-block;
  line-height: 1.7;
  padding: 10px 0 0;
}

.modal-footer {
  padding: 10px 14px 24px;
  text-align: center;
}

.modal-footer button {
  display: inline-block;
  width: 122px;
  height: 40px;
  line-height: 40px;
  border-radius: 3px;
  cursor: pointer;
  transition: all .5s ease;
  font-size: 16px;
}

.modal-footer button + button {
  margin-left: 22px;
}

.modal-footer button:hover {
  opacity: .8;
}

.modal-footer .ok {
  background: #eaa73f;
  color: #fff;
}

.modal-footer .default {
  background: #bbb;
  color: #fff;
}

#toast {
  width: 300px;
  position: fixed;
  z-index: 102;
  left: 50%;
  margin-left: -150px;
  top: 50%;
  margin-top: -60px;
  display: none;
}

#toast .fade-in {
  animation: fadeIn .25s linear  both;
}

#toast .fade-out {
  animation: fadeOut .25s linear  both;
}

#toast .text {
  padding: 6px;
  text-align: center;
  color: #fff;
  line-height: 20px;
  background: #f85300;
  position: relative;
}

.loading {
  z-index: 10;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.loading .loadingImg {
  width: 40px;
  height: 40px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -40px 0 0 -20px;
  background: url(../img/loading.png) no-repeat;
}

.loading .spinner {
  width: 30px;
  height: 30px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -30px 0 0 -15px;
}

.loading .container1 > div,
.loading .container2 > div,
.loading .container3 > div {
  width: 6px;
  height: 6px;
  background-color: #eaa73f;
  border-radius: 100%;
  position: absolute;
  -webkit-animation: bouncedelay 1.2s infinite ease-in-out;
  animation: bouncedelay 1.2s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.loading .spinner .spinner-container {
  position: absolute;
  width: 100%;
  height: 100%;
}

.loading .container2 {
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}

.loading .container3 {
  -webkit-transform: rotateZ(90deg);
  transform: rotateZ(90deg);
}

.loading .circle1 {
  top: 0;
  left: 0;
}

.loading .circle2 {
  top: 0;
  right: 0;
}

.loading .circle3 {
  right: 0;
  bottom: 0;
}

.loading .circle4 {
  left: 0;
  bottom: 0;
}

.loading .container2 .circle1 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.loading .container3 .circle1 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.loading .container1 .circle2 {
  -webkit-animation-delay: -.9s;
  animation-delay: -.9s;
}

.loading .container2 .circle2 {
  -webkit-animation-delay: -.8s;
  animation-delay: -.8s;
}

.loading .container3 .circle2 {
  -webkit-animation-delay: -.7s;
  animation-delay: -.7s;
}

.loading .container1 .circle3 {
  -webkit-animation-delay: -.6s;
  animation-delay: -.6s;
}

.loading .container2 .circle3 {
  -webkit-animation-delay: -.5s;
  animation-delay: -.5s;
}

.loading .container3 .circle3 {
  -webkit-animation-delay: -.4s;
  animation-delay: -.4s;
}

.loading .container1 .circle4 {
  -webkit-animation-delay: -.3s;
  animation-delay: -.3s;
}

.loading .container2 .circle4 {
  -webkit-animation-delay: -.2s;
  animation-delay: -.2s;
}

.loading .container3 .circle4 {
  -webkit-animation-delay: -.1s;
  animation-delay: -.1s;
}

@-webkit-keyframes bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  40% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

body > .loading {
  position: fixed;
  filter: progid:DXImageTransform.Microsoft.gradient(enabled='true', startColorstr='#99FFFFFF', endColorstr='#99FFFFFF');
  background-color: rgba(255, 255, 255, 0.6);
}

:root .loading {
  filter: none;
}

/* 以下根据需要引入 */
.page-turn {
  font-size: 0;
  clear: both;
  color: #666;
  text-align: center;
}

.page-turn * {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
}

.page-turn a {
  height: 32px;
  line-height: 32px;
  text-decoration: none;
  color: #7f7f7f;
}

.page-turn a,
.page-turn span {
  margin: 0 4px 8px;
  padding: 0 12px;
}

.page-turn span {
  height: 32px;
  line-height: 32px;
}

.page-turn input,
.page-turn button {
  border: 1px solid #d5d5d5;
  background-color: #f2f2f2;
}

.page-turn .disabled {
  color: #bfbfbf;
  border-color: #d2d2d2 !important;
}

.page-turn input {
  width: 46px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}

.page-turn button {
  height: 32px;
  line-height: 32px;
  margin-left: 5px;
  padding: 0 10px;
  color: #666;
  cursor: pointer;
}

.page-turn .prev,
.page-turn .next {
  width: 36px;
}

.page-turn .prev i,
.page-turn .next i {
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -5px 0 0 -1px;
}

.page-turn .prev i {
  border-right: 5px solid #999;
  border-top: 5px dashed transparent;
  border-bottom: 5px dashed transparent;
  border-left: 0;
}

.page-turn .next i {
  border-left: 5px solid #999;
  border-top: 5px dashed transparent;
  border-bottom: 5px dashed transparent;
  border-right: 0;
}

.page-turn .prev:hover i {
  border-right-color: #eaa73f;
}

.page-turn .next:hover i {
  border-left-color: #eaa73f;
}

.pink-page a {
  border: 1px solid #d5d5d5;
}

.pink-page input {
  background-color: #fff;
}

.pink-page a,
.pink-page span,
.pink-page input,
.pink-page button {
  border-radius: 2px;
  position: relative;
}

.pink-page .on {
  background-color: #eaa73f;
  border-color: #eaa73f;
  color: #fff;
}

.pink-page input:focus {
  border-color: #999;
}

.pink-page a:hover,
.pink-page button:hover {
  border-color: #eaa73f;
  color: #eaa73f;
}

.banner {
  height: 150px;
  overflow: hidden;
  position: relative;
}

.banner .change {
  width: 44px;
  height: 72px;
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  top: 50%;
  margin-top: -30px;
  cursor: pointer;
  transition: opacity .5s;
  z-index: 2;
  opacity: 0;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity='0');
}

.banner .change i {
  width: 18px;
  height: 38px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -19px 0 0 -9px;
}

.banner .change:hover {
  background: #999;
}

.banner:hover .change {
  opacity: 1;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity='100');
}

.banner > ul {
  width: 100%;
  height: inherit;
}

.banner > ul > li {
  width: 100%;
  height: 150px;
  float: left;
  position: relative;
  overflow: hidden;
}

.banner > ul > li > img {
  width: inherit;
  max-width: 1920px;
  height: inherit;
}

.banner > ul > li > a {
  display: block;
  width: 1920px;
  text-align: center;
  height: 150px;
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -960px;
  background-position: center 0;
  background-repeat: no-repeat;
}

.banner .prev {
  left: 100px;
}

.banner .prev i {
  background: #fff;
}

.banner .next {
  right: 100px;
}

.banner .next i {
  background: #fff;
}

.banner .number {
  width: 100%;
  text-align: center;
  font-size: 0;
  position: absolute;
  left: 0;
  bottom: 20px;
  z-index: 2;
}

.banner .number span {
  width: 10px;
  height: 10px;
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  margin-left: 12px;
  cursor: pointer;
  border-radius: 10px;
  opacity: 0.7;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity='70');
  position: relative;
  transition: width .2s;
}

.banner .number span:first-child {
  margin-left: 0;
}

.banner .number span:hover {
  opacity: 1;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity='100');
}

.banner .number span.on {
  width: 20px;
  height: 8px;
  opacity: 1;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity='100');
}

.banner-opacity > ul > li {
  position: absolute;
  left: 0;
  top: 0;
}
